const sendToBackEnd = async (values) => {
  try {
    let result = await api.request({
      url: "/addFornecedor",
      data: values,
      method: "POST"
    })
    console.log("resposta do servidor: ", result)
    if (Object.keys(result.data).length == 0) {
      // se for copiar para cadastro de cliente preste ateno nesta mensagem
      message = "Sem resposta do servidor"
      console.log("result data is empty")
      error = true
      await auth.currentUser.delete()
    } else {
      if (!result.data.error) {
        error = false
        message = "Estamos enviando um email de confirmao, aguarde...."

        
        // haver um login automtico mas como o email ainda no foi confirmado a tela de confirmao de e-mail vai abrir
        // remover qualquer usurio do async storage
       
        // avisar ao app que o proximo login ser o login de um usurio que acabou de ser cadastrado.  s para no abrir o introSlider de novo. 
      } else {
        console.log("resposta do servidor: ", result.data)
        error = true
        message = result.data.message
        await auth.currentUser.delete()
      }
    }
  } catch (e) {
    
    
    message = "Sem resposta do servidor"
    console.log("erro do axios: ", e)
  }
  
}
const updateFirebaseId = async (uid, email) => {
  try{
    let result = await api.request({
      url: "/updateFirebaseId/"+uid+"/"+email,
      method: "GET"
    })
  }catch(e){
    console.log("erro no update firebase id", e)
  }
 
}